import alt from '../alt';
import RoutingActions from '../actions/RoutingActions';

const matchAll = /.*/;

class RoutingStore {
  constructor() {
    this.bindActions(RoutingActions);

    this.searchTerm = /.*/;
    this.categoryId = '';
    this.productId = '';
  }
  onShowCategory(id) {
    this.categoryId = id;
  }
  onShowProduct(id) {
    this.productId = id;
  }
  onSearch(term) {
    this.searchTerm = term === '' ? matchAll : new RegExp(term);
  }
}

export default alt.createStore(RoutingStore);




