import React from 'react';
import { PureRenderMixin } from 'react/addons';
import { map } from 'lodash-compat';

var { PropTypes } = React;

var InputSelect = React.createClass({
  mixins: [PureRenderMixin],
  getDefaultProps() {
    return {
      value: '',
      placeholder: '選我',
      options: [0, 1, 2]
    };
  },
  _renderOptions(option, i) {
    return <li key={i}>{option}</li>
  },
  render() {
    var { className = '', children = [], value, placeholder, options } = this.props;
    value = value && value.length ? value : placeholder;
    return (
      <div className={`yfs-comp input-select ${className}`}>
        <div className="value">{value}</div>
        <a className="handler" href="#"><i className="fa fa-chevron-down"></i></a>
        <ol className="options">
          {map(options, this._renderOptions)}
        </ol>
      </div>
    );
  }
});

export default InputSelect;




