import React from 'react';
import { PureRenderMixin } from 'react/addons';

var { PropTypes } = React;

var Breadcrumb = React.createClass({
  mixins: [PureRenderMixin],
  _renderPath(child, i) {
    return <li key={i}>{child}</li>;
  },
  render() {
    var { className = '', children = [], path } = this.props;
    return (
      <ol className={`yfs-comp breadcrumb ${className}`}>
        {children.map ? children.map(this._renderPath) : <li>{children}</li>}
      </ol>
    );
  }
});

export default Breadcrumb;



