import React from 'react';
import { Link } from 'react-router-component';

var SubCategory = React.createClass({
  getDefaultProps() {
    return {
      title: '無以名狀',
      children: []
    };
  },
  _renderChild({ id, title }, i) {
    return <li key={i}><Link href={`/category/${id}`}>{title}</Link></li>;
  },
  render() {
    var { children = [], title, categories } = this.props;
    return (
      <div className={`yfs-comp sub-category ${!children.length ? 'empty' : ''}`}>
        <h2 className="title">
          <i className="fa fa-chevron-right"></i>
          {title}
        </h2>
        <ol className="children">
          {children.map(this._renderChild)}
        </ol>
      </div>
    );
  }
});

export default SubCategory;




