import React from 'react';
import Product from './Product';

var HotProducts = React.createClass({
  getDefaultProps() {
    return {
      products: []
    };
  },
  _renderProducts(product, i) {
    return <Product key={i} {...product} />;
  },
  render() {
    var { children = [], products } = this.props;
    return (
      <div className="yfs-comp hot-products tiny">
        <h2 className="title">
          <i className="fa fa-chevron-right"></i>
          熱銷商品
        </h2>
        {products.map(this._renderProducts)}
        {children}
      </div>
    );
  }
});

export default HotProducts;




