'use strict';

import React from 'react';
import Header from './Header';
import IndexPage from './IndexPage';
import SearchPage from './SearchPage';
import CategoryPage from './CategoryPage';
import ProductPage from './ProductPage';
import Footer from './Footer';
import { RounterMixin, Locations, Location, Link } from 'react-router-component';

import PromotionStore from '../stores/PromotionStore';

var App = React.createClass({
  render() {
    return (
      <div className="yfs-comp app">
        <Header />
        <Locations hash className="content">
          <Location path="/" handler={IndexPage} />
          <Location path="/search/?(:term)" handler={SearchPage} />
          <Location path="/category/?(:id)" handler={CategoryPage} />
          <Location path="/product/?(:id)" handler={ProductPage} />
        </Locations>
        <Footer />
      </div>
    );
  }
});

export default App;




