import React from 'react';
import { PureRenderMixin } from 'react/addons';
import ListenerMixin from 'alt/mixins/ListenerMixin';
import SubCategory from '../SubCategory';
import RoutingStore from '../../stores/RoutingStore'
import CategoryStore from '../../stores/CategoryStore';
import { cloneDeep, filter } from 'lodash-compat';

var SubCategoryContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    var store = CategoryStore.getState();
    return {
      id: RoutingStore.getState().categoryId,
      handler: store.handler
    };
  },
  componentDidMount() {
    this.listenTo(RoutingStore, this._onSearchChange);
    this.listenTo(CategoryStore, this._onCategoryChange);
  },
  _onSearchChange() {
    this.setState({ id: RoutingStore.getState().categoryId });
  },
  _onCategoryChange() {
    var store = CategoryStore.getState();
    this.setState({
      handler: store.handler
    });
  },
  render() {
    var { id, handler } = this.state,
        category = cloneDeep(handler.get(id));

    if (category) {
      let children = category.children.map(id => handler.get(id));
      category.children = filter(children);
    }

    return <SubCategory {...category}/>;
  }
});

export default SubCategoryContainer;




