import React from 'react';
import { PureRenderMixin } from 'react/addons';

var { PropTypes } = React;

var InputNumber = React.createClass({
  mixins: [PureRenderMixin],
  getDefaultProps() {
    return {
      value: 0
    };
  },
  render() {
    var { className = '', children = [], value } = this.props;
    return (
      <div className={`yfs-comp input-number ${className}`}>
        <a className="minus" href="#"><i className="fa fa-minus"></i></a>
        <div className="value">{value}</div>
        <a className="plus" href="#"><i className="fa fa-plus"></i></a>
      </div>
    );
  }
});

export default InputNumber;




