import React from 'react';
import { Link } from 'react-router-component';
import { Menu, MenuItem } from '../Menu';

var CategoryList = React.createClass({
  getDefaultProps() {
    return {
      categories: []
    };
  },
  _compose(item, i) {
    var { children, id, title } = item;
    return (
      (!children || !children[0]) ?
        null :
      !children[0].id ?
        <MenuItem key={i}><Link globalHash href={`/category/${id}`}>{title}</Link></MenuItem> :
      // otherwise
        <Menu key={i} title={title}>{children.map(this._compose)}</Menu>
    );
  },
  render() {
    var { children, categories } = this.props;
    return (
      <Menu className="category-list" title="全部商品">
        {categories.map(this._compose)}
      </Menu>
    );
  }
});

export default CategoryList;




