import React from 'react';
import { PureRenderMixin } from 'react/addons';
import ListenerMixin from 'alt/mixins/ListenerMixin';
import RoutingStore from '../../stores/RoutingStore';
import CategoryStore from '../../stores/CategoryStore';
import ProductStore from '../../stores/ProductStore';
import SearchResult from '../SearchResult';
import { flatten } from 'lodash-compat';

function getAllProducts(handlerC, storeP, id) {
  var target;
  return (
    (target = handlerC.get(id)) ?
      flatten(target.children.map(c => getAllProducts(handlerC, storeP, c))) :
    (target = storeP.products[storeP.indexes[id]]) ?
      target :
    // otherwise
      undefined
  );
}



var CategoryResultContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    return {
      id: RoutingStore.getState().categoryId,
      handlerC: CategoryStore.getState().handler,
      storeP: ProductStore.getState()
    };
  },
  componentDidMount() {
    this.listenTo(RoutingStore, this._onRoutingChange);
    this.listenTo(CategoryStore, this._onCategoriesChange);
    this.listenTo(ProductStore, this._onProductsChange);
  },
  _onRoutingChange() {
    this.setState({ id: RoutingStore.getState().categoryId });
  },
  _onCategoriesChange() {
    this.setState({ handlerC: CategoryStore.getState().handler });
  },
  _onProductsChange() {
    this.setState({ storeP: ProductStore.getStaet() });
  },
  render() {
    var { id, handlerC, storeP } = this.state,
        products = getAllProducts(handlerC, storeP, id);

    return <SearchResult {...{ products }}/>;
  }
});

export default CategoryResultContainer;




