import React from 'react';
import { PureRenderMixin } from 'react/addons';
import RoutingActions from '../../actions/RoutingActions'
import BreadcrumbContainer from './BreadcrumbContainer';
import HotProductsContainer from '../HotProductsContainer';
import ProductDetail from './ProductDetail';

var { PropTypes } = React;

var ProductPage = React.createClass({
  mixins: [PureRenderMixin],
  componentDidMount() {
    RoutingActions.showProduct(this.props.id);
  },
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.id !== this.props.id) {
      RoutingActions.showProduct(this.props.id);
    }
  },
  render() {
    var { className = '', children = [] } = this.props;
    return (
      <div className={`yfs-comp page product-page ${className}`}>
        <BreadcrumbContainer />
        <div className="left">
          <HotProductsContainer />
        </div>
        <ProductDetail />
        {children}
      </div>
    );
  }
});

export default ProductPage;




