import React from 'react';
import { PureRenderMixin } from 'react/addons';
import ListenerMixin from 'alt/mixins/ListenerMixin';
import HotProducts from './HotProducts';
import ProductStore from '../stores/ProductStore';

var HotProductsContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    return ProductStore.getState();
  },
  componentDidMount() {
    this.listenTo(ProductStore, this._onChange);
  },
  _onChange() {
    this.setState(ProductStore.getState());
  },
  render() {
    var { children = [] } = this.props;
    var { products } = this.state;
    products = products.slice(0, 3);
    return <HotProducts {...{ children, products }} />;
  }
});

export default HotProductsContainer;




