import React from 'react';
import { PureRenderMixin } from 'react/addons';

var { PropTypes } = React;

var MoreInfo = React.createClass({
  mixins: [PureRenderMixin],
  render() {
    var { className = '', children = [] } = this.props;
    // XXX: mockup only
    return (
      <div className={`yfs-comp more-info ${className}`}>
        <ol className="tabs">
          <li><a className="actived" href="#">產品說明</a></li>
          <li><a href="#">商品規格</a></li>
          <li><a href="#">商品特色</a></li>
          <li><a href="#">注意事項</a></li>
        </ol>
        <ol className="contents">
          <li>
            <div>
              亮眼可愛的外型下，卻有大曬衣架，小巧大容量。
            </div>
          </li>
        </ol>
      </div>
    );
  }
});

export default MoreInfo;




