import React from 'react';
import { PureRenderMixin } from 'react/addons';
import CategoryList from './CategoryList';
import CategoryStore from '../../stores/CategoryStore';
import ListenerMixin from 'alt/mixins/ListenerMixin';

function treeFromCategory(handler, cat, parent) {
  // although the categories have they parent id, find them from the indexes are
  // more expensive then just pass them down
  return {
    id: cat.id,
    parent: parent,
    title: cat.title,
    children: cat.children.map(id => {
      var next = handler.get(id);
      return next !== undefined ?
        treeFromCategory(handler, next, cat) : // a category
        id ;                                   // a product
    })
  }
}

var CategoryListContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    var { handler } = CategoryStore.getState(),
        menu = handler.last();
    return { categories: treeFromCategory(handler, menu).children };
  },
  componentDidMount() {
    this.listenTo(CategoryStore, this._onChange);
  },
  _onChange() {
    this.setState(this.getInitialState());
  },
  render() {
    return <CategoryList {...this.state} />;
  }
});

export default CategoryListContainer;




