import React from 'react';
import { PureRenderMixin } from 'react/addons';
import BreadcrumbContainer from './BreadcrumbContainer';
import RoutingActions from '../../actions/RoutingActions';
import SubCategoryContainer from './SubCategoryContainer';
import HotProductsContainer from '../HotProductsContainer';
import CategoryResultContainer from './CategoryResultContainer';

var CategoryPage = React.createClass({
  mixins: [PureRenderMixin],
  componentDidMount() {
    RoutingActions.showCategory(this.props.id);
  },
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.id !== this.props.id) {
      RoutingActions.showCategory(this.props.id);
    }
  },
  render() {
    var { className = '', children = [] } = this.props;
    return (
      <div className={`yfs-comp page category-page ${className}`}>
        <BreadcrumbContainer />
        <div className="left">
          <SubCategoryContainer />
          <HotProductsContainer />
        </div>
        <CategoryResultContainer />
        {children}
      </div>
    );
  }
});

export default CategoryPage;




