import React from 'react';
import { PureRenderMixin } from 'react/addons';
import ListenerMixin from 'alt/mixins/ListenerMixin';
import SearchResult from '../SearchResult';
import RoutingStore from '../../stores/RoutingStore';
import ProductStore from '../../stores/ProductStore';
import { filter } from 'lodash-compat';

var SearchResultContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    return {
      products: ProductStore.getState().products,
      searchTerm: RoutingStore.getState().searchTerm
    }
  },
  componentDidMount() {
    this.listenTo(ProductStore, this._onProductsChange);
    this.listenTo(RoutingStore, this._onSearchTermChange);
  },
  _onProductsChange() {
    this.setState({ products: ProductStore.getState().products });
  },
  _onSearchTermChange() {
    console.log('change');
    this.setState({ searchTerm: RoutingStore.getState().searchTerm });
  },
  render() {
    var { products, searchTerm } = this.state;
    products = filter(products, p => searchTerm.test(p.title));
    return <SearchResult {...{ products }} />
  }
});

export default SearchResultContainer;




