import React from 'react';
import { PureRenderMixin } from 'react/addons';
import ListenerMixin from 'alt/mixins/ListenerMixin';
import { Link } from 'react-router-component';
import RoutingStore from '../../stores/RoutingStore';
import CategoryStore from '../../stores/CategoryStore';
import ProductStore from '../../stores/ProductStore';
import Breadcrumb from '../Breadcrumb';
import { map } from 'lodash-compat';

function getProductParent(categories, pid) {
  var parent = null;

  for (let category of categories) {
    for (let child of category.children) {
      if (child === pid) {
        parent = category;
      }
    }
  }

  return parent;
}

// XXX: duplicated
function getAncestors(handler, category) {
  if (category && category.parent) {
    var { parent } = category,
        cat = handler.get(parent);
    return getAncestors(handler, cat).concat([category]);
  }
  return [];
}

var BreadcrumbContainer = React.createClass({
  mixins: [PureRenderMixin, ListenerMixin],
  getInitialState() {
    return {
      id: RoutingStore.getState().productId,
      storeC: CategoryStore.getState(),
      storeP: ProductStore.getState()
    };
  },
  componentDidMount() {
    this.listenTo(RoutingStore, this._onSearchChange);
    this.listenTo(CategoryStore, this._onCategoriesChange);
    this.listenTo(ProductStore, this._onProductsChange);
  },
  _onSearchChange() {
    this.setState({ id: RoutingStore.getState().productId });
  },
  _onCategoriesChange() {
    this.setState({ storeC: CategoryStore.getState() });
  },
  _onProductsChange() {
    this.setState({ storeP: ProductStore.getState() });
  },
  _renderPath(category, i) {
    var { id, title } = category;
    return <Link key={i + 1} href={`/category/${id}`}>{title}</Link>;
  },
  render() {
    var { id, storeC, storeP } = this.state,
        cat = getProductParent(storeC.categories, id),
        pro = storeP.products[storeP.indexes[id]],
        path = getAncestors(storeC.handler, cat),
        children = map(path, this._renderPath);
      children.unshift(<Link key="0" href="/">首頁</Link>);
      if (pro) { // test if product exist if
        children.push(<Link key={children.length} href={`/product/${pro.id}`}>{pro.title}</Link>);
      }
    return <Breadcrumb>{children}</Breadcrumb>;
  }
});

export default BreadcrumbContainer;




