import React from 'react';
import { Link } from 'react-router-component';

var Product = React.createClass({
  getDefaultProps() {
    return {
      id: '',
      title: '沒有商品',
      image: 'http://placekitten.com/g/150/150',
      points: 0,
      dollars:  0,
      mixed: {
        points: 0,
        dollars:  0
      },
      layaway: {
        dollars: 3333,
        installments: 3
      }
    };
  },
  render() {
    var { className = '', children = [],
          id, image, title, points, dollars, mixed } = this.props;
    return (
      <div className={`yfs-comp product ${className}`}>
        <figure>
          <img className="image" src={image}></img>
          <div className="faviorite"></div>
        </figure>
        <Link className="title" href={`/product/${id}`}>{title}</Link>
        <div className="points">{points}</div>
        <div className="dollars">{dollars}</div>
        <div className="mixed">
          <span className="points">{mixed.points}</span>
          +
          <span className="dollars">{mixed.dollars}</span>
        </div>
        <div className="buy"><button className="text"></button></div>
        {children}
      </div>
    );
  }
});

export default Product;




