import alt from '../alt';
import uuid from 'node-uuid';
import { zh } from 'lesstext';
//should import actions here

/**
 * XXX: Polyfilled Map and Alt
 **
 * One can use 'babel/polyfill' to enable `Map` in IE8, but it will cause:
 * "Circular reference in value argument not supported" when Alt try to save its
 * first snapshot.
 *
 * I can not locate this bug, so no issue will be open.
 *
 * Please check `const saveInitialSnapshot` in `alt.js` for more info:
 *   https://github.com/goatslacker/alt/blob/d1bb34914b8a1d7ba1028c251e1cd2b1e2b3e8c6/src/alt.js#L257
 */
function createPromotions(count = 4) {
  var indexes = {},
      collection = [];//new Map();

  for (let i = 0; i < count; ++i) {
    let id = uuid.v4();
    indexes[id] = collection.length;
    collection.push({
      id: id,
      title: zh.loremWords(10),
      image: 'http://placekitten.com/g/480/170',
    });
  }

  collection.get = id => collection[indexes[id]];

  return collection;
}

class PromotionStore {
  constructor() {
    this.promotions = createPromotions();
    //alert(this.promotions.get(this.promotions[0].id).title);
  }
}

export default alt.createStore(PromotionStore);




